import React, { useState } from "react";
import styled from "styled-components";

import MapView from "../components/mapView";
import ScatterView from "../components/scatterView";

import Card from "../components/cards";
import Page from "../components/page";

const Container = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
`;

const Cards = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  width: 70%;
`;

const Trivariate = () => {
  const width = 380;
  const height = 250;
  const mapsTranslate: [number, number] = [width / 2, height / 2];
  const mapsScale = 500;

  const mapTitles = ["Cuebiq 1", "Cuebiq 2", "Safegraph 1", "Safegraph 2"];

  const MapSettings = {
    width,
    height,
    translate: mapsTranslate,
    scale: mapsScale,
  };

  const [selectedCounty, setSelectedCounty] = useState<[string, string]>(["", ""]);

  const [selectedState, setSelectedState] = useState("");

  return (
    <Page>
      <Container>
        <MapView
          MapSettings={MapSettings}
          selectedCounty={selectedCounty}
          mapTitles={mapTitles}
          setSelectedCounty={setSelectedCounty}
          selectedState={selectedState}
          setSelectedState={setSelectedState}
        />
        <Cards>
          {mapTitles.map((title, i) => {
            return <Card key={i} title={title} />;
          })}
        </Cards>
        <ScatterView />
      </Container>
    </Page>
  );
};

export default Trivariate;
